import React, {Component} from 'react'
import './style.css'

export default class Notfound extends Component {

    render() {
        return (
            <div className={'div_not'}>
                <h1>403</h1>
                <p>抱歉，你访问的地址不存在！</p>
            </div>
        )
    }
}


