import React, {Component} from 'react'
import {Flex} from 'antd-mobile';
import Nodate from '../../../components/Nodate'
import PageLoading from '../../../components/PageLoading'
import {getLightcolor, Userid} from "../../../components/Utils";
import {getleaderTaskDeptInfo, getTaskDeptInfo} from '../../../request/request'

async function getData() {

    let attrs = JSON.parse(this.props.match.params.id);
    let params = {...Userid(), 'id': attrs.id};
    let d = [];
    if (attrs['view'] === '任务视图') {
        d = await getTaskDeptInfo(params)
    } else if (attrs['view'] === '领导视图') {
        d = await getleaderTaskDeptInfo(params)
    }
    this.setState({data: d.data, loading: false})
}

export default class Taskdetails extends Component {

    state = {
        data: [],
        loading: true
    };

    goto = (id) => {
        let t = JSON.parse(this.props.match.params.id)
        t['id'] = id
        this.props.history.push({pathname: `/view/Taskinfo/${JSON.stringify(t)}`})
    };

    componentDidMount() {
        document.title = '督查任务详情';
        getData.call(this)
    }

    render() {
        const {data, loading} = this.state
        return (
            <div>
                <PageLoading loading={loading}/>
                {
                    data && data.length > 0 ? data.map((v, i) => <div key={i} className='div_piece_code'
                                                                      style={{padding: 6}}>
                        <Flex justify="between">
                            <div className='div_piece_title' onClick={() => this.goto(v['id'])}>{v['责任单位']}</div>
                            <div className='div_piece_state'
                                 style={{backgroundColor: getLightcolor(v.state)}}>{v['任务办理进度']}</div>
                        </Flex>
                        <div style={{display: 'Flex', justifyContent: 'start', marginLeft: 24}}>{v['督查件标题']}</div>
                        <div style={{display: 'Flex', justifyContent: 'flex-end', marginRight: 12}}>
                            <span>{v['办结时间'] ? `办结时间：${v['办结时间']}` : ""}</span></div>
                    </div>) : !this.state.loading ? <Nodate msg={'无数据'}/> : ''
                }
            </div>
        )
    }
}


