import React, {Component} from 'react'

const objSize = {xs: 3, sm: 6, md: 9, lg: 15, xl: 21}
export default class BlackSpace extends Component {

    render() {
        // 上下留白，支持自定义高度和颜色
        const {size, height, bgcolor} = this.props
        const h = height || objSize[size];
        return (
            <div style={{height: h, backgroundColor: bgcolor || '#e8eaec'}}></div>
        )
    }
}


