//返回状态判断(添加响应拦截器)
axios.interceptors.response.use((res) => {
    //对响应数据做些事
    //请求响应之后对数据可以做相应的处理
    // console.log(res)
    // console.log(res.data);
    if (!res) {
        return Promise.reject(res);
    }

    return res;
}, (error) => {
    return { data: {} };
});


//获取用户信息
export const getUserInfo = (params) => {
    return axios.get(idsccs + '/GovWX/worktask/getuserright', { params: params });
};

