import React from 'react'
import {Route, Switch} from 'react-router-dom';
import Home from '../containers/home/index'
import Dutybench from '../containers/home/Dutybench'
import Handlebench from '../containers/home/Handlebench'
import Leadbench from '../containers/home/Leadbench'
import Leadview from "../containers/view/leadview";
import Dcpiece from "../containers/view/leadview/dcpiece";
import Task from "../containers/view/leadview/task";
import Taskdetails from "../containers/view/leadview/taskdetails";
import Taskinfo from "../containers/view/leadview/taskInfo";
import Taskfeedback from "../containers/view/leadview/taskfeedback";
import Unitview from "../containers/view/unit";
import Finishview from "../containers/view/finish"
import Notfound from "../components/Nodate/Notfound";
import Notice from "../containers/notice/index";

//const Notice =()=>require('../containers/notice/index')

/*
switch 只匹配一个路由
exact 严格匹配
path 为空是匹配所有路由
 */
export const routers = () =>
    [
        <Route exact name='首页' path="/" component={Home} key="home"/>,
        <Route exact name='领导首页' path="/home/Leadbench" component={Leadbench} key="Leadbench"/>,
        <Route exact name='经办人首页' path="/home/Handlebench" component={Handlebench} key="Handlebench"/>,
        <Route exact name='责任单位首页' path="/home/Dutybench" component={Dutybench} key="Dutybench"/>,
        <Route exact name='领导视图' path="/view/Leadview" component={Leadview} key="Leadview"/>,
        <Route exact name='督查件' path="/view/dcpiece/:attrs?/:tabs?/:type?/:view?" component={Dcpiece} key="Dcpiece"/>,
        <Route exact name='领导视图-督查任务' path="/view/Task/:attrs?/:state?" component={Task} key="Task"/>,
        <Route exact name='领导视图-督查任务详情' path="/view/Taskdetails/:id?" component={Taskdetails} key="Taskdetails"/>,
        <Route exact name='领导视图-督查任务-详情' path="/view/Taskinfo/:id?" component={Taskinfo} key="Taskinfo"/>,
        <Route exact name='领导视图-督查任务-进度反馈' path="/view/Taskfeedback/:id?" component={Taskfeedback} key="Taskfeedback"/>,
        <Route exact name='单位视图' path="/view/Unitview" component={Unitview} key="Unitview"/>,
        <Route exact name='归档查询' path="/view/Finishview" component={Finishview} key="Finishview"/>,
        <Route exact name='通告内容' path="/notice/index/:id" component={Notice} key="Finishview"/>,
        <Route component={Notfound} key='Notfound'/>
    ];





