import React, {Component} from 'react'
import people from '../../../images/peopledef.png'
import './style.css'
import BlackSpace from '../../../components/BlackSpace'
import PageLoading from '../../../components/PageLoading'
import Processdetails from '../../../components/Processdetails'
import {WingBlank,ActivityIndicator} from 'antd-mobile';
import Listbox from '../../../components/List'
import {
    getRespLeadCount, getLeadUserInfo, getprovincapprovalTotal, getspecialreport,
    GetProvLeadCount
} from '../../../request/request'
import {Userid} from "../../../components/Utils";

let process = [
    {title: '省长批示件', processList: [{state: '逾期未结', num: 0}, {state: '正常在办', num: 0}]},
    {title: '分管（协调、联系）', processList: [{state: '逾期未结', num: 0}, {state: '正常在办', num: 0}]}
];

// NProgress.configure({ easing: 'ease', speed: 500 });

async function getData() {
    let d = await getLeadUserInfo({'type': 1});
    let g = await  getRespLeadCount(Userid());
    let provincapprovalTotal = await GetProvLeadCount(Userid());
    let userInfo;
    //let provincapprovalTotal = await getprovincapprovalTotal(Userid());
    if (provincapprovalTotal) {
        process[0].processList[0].num = provincapprovalTotal.data['逾期未结'] || 0;
        process[0].processList[1].num = provincapprovalTotal.data['正常在办'] || 0
    }
    if (g) {
        process[1].processList[0].num = g.data['逾期未结'] || 0;
        process[1].processList[1].num = g.data['正常在办'] || 0
    }
    if (d && Object.keys(d.data).length!==0) {
        userInfo=d.data.filter(v=>v.userid===Userid().userid)[0]
    }
    this.setState({userInfo:userInfo,loading:false })
}

export default class Leadview extends Component {

    state = {
        userInfo: {},
        specialreport: {},
        loading:true
    };

    componentDidMount() {
        getData.call(this);
        document.title = '领导视图'
    }

    render() {
        return (
            <div>
                <PageLoading loading={this.state.loading} />
                <Hend {...this.state}/>
                <BlackSpace size='lg'/>
                <WingBlank>
                    <Processdetails title='省长批示件' process={process}
                                    restProcess={(e) => this.props.history.push(`/view/dcpiece/true/false/${JSON.stringify(e)}/领导视图`)}/>
                </WingBlank>
                <BlackSpace size='lg'/>
                <Listbox title='督查专报'/>
            </div>
        )
    }
}

class Hend extends Component {
    render() {
        const {userInfo} = this.props;
        return (
            <div className="flex-container" style={{justifyContent: 'start', padding: '16px 16px 16px 20px',backgroundColor:'white'}}>
                <div>
                    <img alt={''} src={userInfo?userInfo.photourl || people:people} style={{height: '50pt', width: '50pt'}}></img>
                </div>
                <div className="div_hend">
                    <div>{userInfo?userInfo.name || '':''}</div>
                    <div>{userInfo?userInfo.type || '':''}</div>
                </div>
            </div>
        )
    }
}


