import React, {Component} from 'react'
import {getTaskicon} from '../Utils'
import {Link} from "react-router-dom";
import {Tabs} from 'antd-mobile';
import {routers} from "../../Routers/Routermanage";
import './style.css'

// 任务功能列表
//每页显示4个，超过4个需分页显示
export default class Taskbox extends Component {

    render() {
        const {taskList} = this.props;
        return (
            <div>
                {
                    taskList.length <= 4 ? this.tasklist(taskList) :
                        <Tasktab list={taskList}>{(d, k) => this.tasklist(d, k)}</Tasktab>
                }
            </div>
        )
    }

    tasklist = (d, k = 0) => {
        return <div className="flex-container" key={k} style={{height: 120}}>{
            d.map((v, i) =>
                <div key={i} className="flex_taskbox">
                    <Link to={v ? this.path(v) : ''}>
                        {
                            v.length > 0 ? <img alt='' src={getTaskicon(v)} style={{
                                height: 56,
                                width: 56
                            }}/> : ''
                        }
                        <div className='div_task_title'>{v}</div>
                    </Link>
                </div>)
        }
        </div>
    };

    path = (e) => {
        if (e === '任务视图') {
            return '/view/dcpiece/false/false/false/任务视图'
        }
        let p = routers().filter(v => v.props['name'] === e)[0];
        if (p) {
            return p.props['path']
        }
        return ''
    }
}

class Tasktab extends Component {
    render() {
        const {list} = this.props;
        let tabs = [];
        let c = list.length % 4;
        let p = parseInt(list.length / 4)
        let tabc = c > 0 ? p + 1 : p;
        for (let i = 0; i < tabc; i++) {
            tabs.push({
                title: <div className='tabs_num'>一</div>, sub: i
            })
        }
        return (
            <div className='div_tabs_update'>
                <Tabs tabs={tabs} tabBarPosition={'bottom'} tabBarInactiveTextColor={'#D6D7DC'}>
                    {
                        tabs.map(t => this.props.children(list.filter((v, i) => i >= t.sub * 4 && i <= (t.sub + 1) * 4 - 1), t.sub))
                    }
                </Tabs>
            </div>
        )
    }
}


