import React, {Component} from 'react'
import {Badge} from 'antd-mobile';
import Taskbox from '../../../components/Task'
import {
    getDeptTaskInfoDetail,
    getTaskCount,
    getTaskCountFortaskview,
    getTaskInfo,
    getTaskInfoFortaskview
} from '../../../request/request'
import {EnumTaskstate, Userid} from "../../../components/Utils";

async function getData() {
    let attrs = JSON.parse(this.props.match.params.attrs);
    let params = {...Userid(), 'dcjID': String(attrs.id)};
    let d = {};
    if (attrs.view === '任务视图' || attrs.view === '单位视图') {
        d = await getTaskCountFortaskview(params);
    }
    // else if (attrs.view === '单位视图') {
    //     params = {...Userid(), type: attrs.depttype === '省直部门' ? 1 : 2};
    //     d =await getTaskTotalForunit(params)
    //     console.log(d)
    // }
    else {
        d = await getTaskCount(params);
    }

    if (d && d.data && Object.keys(d.data).length > 0) {
        this.setState({data: d.data})
    }
    getTaskinfo.call(this)
}

async function getTaskinfo() {
    let {type, tab} = this.state;
    let attrs = JSON.parse(this.props.match.params.attrs);
    let params = {...Userid(), dcjID: attrs.id, type: EnumTaskstate(type)};
    let task = {};
    if (attrs.view === '任务视图') {
        task = await getTaskInfoFortaskview(params)
    } else {
        if (tab.findIndex(v => v.t === type) === -1) {
            if (attrs.deptname) {
                params = {deptname: attrs.deptname, id: attrs.id};
                task = await getDeptTaskInfoDetail(params);
                if (task && task.data) {
                    task.data = task.data.filter(v => v.state === type);
                }
            } else {
                task = await getTaskInfo(params)
            }
        }
    }
    tab.push({'t': type, data: task.data});
    this.setState({tab})
}

export default class Task extends Component {

    state = {
        data: {'按时在办': 0, '即将到期': 0, '逾期未结': 0, '已办结': 0},
        type: '即将到期',
        tab: []
    };

    taskinfo = (id) => {
        let attrs = JSON.parse(this.props.match.params.attrs);
        attrs['id'] = id;
        this.props.history.push({pathname: `/view/Taskdetails/${JSON.stringify(attrs)}`})
    };

    componentDidUpdate(prevProps, prevState) {
        if (prevState.type != this.state.type && this.state.tab.findIndex(v=>v.t==this.state.type)===-1) {
            getTaskinfo.call(this)
        }
    }

    componentDidMount() {
        document.title = '督查任务';
        getData.call(this)
    }

    render() {
        const {data, tab} = this.state
        let {state, attrs} = this.props.match.params;
        let tabs = [];
        for (let key in data) {
            tabs.push({title: <Badge text={data[key]}>{key}</Badge>})
        }
        const obj = JSON.parse(attrs).type === 'true' ? ['点赞', '关注', '批示'] : [];
        return (
            <div>
                <Taskbox tabs={tabs} tab={tab} attrs={obj} state={state} depttype={'牵头单位'}
                         defaultActiveKey={this.state.type}
                         setStatetype={(tab) => this.setState({type: tab.title.props.children})}
                         taskinfo={(id) => this.taskinfo(id)}>
                </Taskbox>
            </div>
        )
    }
}


