import React, { Component } from 'react'
import Headcarousel from '../../../components/Headcarousel'
import Taskbox from '../../../components/Taskbox'
import BlackSpace from '../../../components/BlackSpace'
import Processdetails from '../../../components/Processdetails'
import Listbox from '../../../components/List'
import { LeadtaskList } from '../../../components/Utils'
import { getdivisionTota, GetProvLeadCount, getRespLeadCount } from "../../../request/request";
import { Userid } from '../../../components/Utils'
import './style.css'
let process = [
    { title: '省长批示件', processList: [{ state: '逾期未结', num: 0 }, { state: '正常在办', num: 0 }] },
    { title: '分管（协调、联系）', processList: [{ state: '逾期未结', num: 0 }, { state: '正常在办', num: 0 }] }
];

async function getData() {
    let d = await getRespLeadCount(Userid());
    let c = await GetProvLeadCount(Userid());
    if (d && d.data) {
        this.setState({ divisionTotal: d.data, provledadcount: c.data })
    }
}

export default class Leadbench extends Component {

    state = {
        divisionTotal: {},
        provledadcount: {},
        loading: true
    }
    componentDidMount() {
        document.title = '电子督查系统';
        getData.call(this)
    }
    render() {
        const { specialreport } = this.props;
        const { divisionTotal, loading, provledadcount } = this.state;
        process[0].processList[0].num = provledadcount['逾期未结'] || 0;
        process[0].processList[1].num = provledadcount['正常在办'] || 0;
        process[1].processList[0].num = divisionTotal['逾期未结'] || 0;
        process[1].processList[1].num = divisionTotal['正常在办'] || 0;
        return (
            <div style={{ backgroundColor: 'white' }}>
                <Headcarousel tip='广州、湛江市提请省支持事项进展情况持事项进展情况持事项进展情况'
                    {...this.props} />
                <About />
                <BlackSpace size='lg' />
                <Taskbox taskList={LeadtaskList} />
                <BlackSpace size='lg' />
                <Processdetails title='省长批示件' process={process} restProcess={(e) => this.props.history.push(`/view/dcpiece/true/false/${JSON.stringify(e)}/领导视图`)} />
                <BlackSpace size='lg' />
                <Listbox title='督查专报'   {...specialreport} />
                <Listbox title='督查通报' msg='广州、湛江市提请省支持事项进展情况广州、湛江市提请省支持事项进展情况' />
                <BlackSpace size='lg' />
            </div>
        )
    }
}

class About extends Component {

    render() {
        const cls = (i) => i < 2 ? 'div_border_right' : '';
        const about = ['点赞', '关注', '批示'];

        // Function as children
        const Info = (restProps) => {
            let items = [];
            for (let i = 0; i < restProps.num; i++) {
                items.push(restProps.children(i))
            }
            return items
        }

        return (
            <div className="flex-container" style={{ height: 78 }}>
                <Info num={3}>
                    {(i) =>
                        <div key={i} className="aboutinfo">
                            <div className={cls(i)}>我的{about[i]}</div>
                            <div className={cls(i)}>{i}</div>
                        </div>
                    }
                </Info>
            </div>
        )
    }
}



