import React, {Component} from 'react'
import {Flex} from 'antd-mobile';
import BlackSpace from '../../components/BlackSpace'
import {getDateFormat, getLightcolor} from '../Utils'
import './style.css'

const Item = Flex.Item;

export default class Dcpiecebox extends Component {

    render() {
        const {state, code, title, type, date} = this.props;
        return (
            <div className='div_piece_code'>
                <Flex justify="between">
                    <Item>{code}</Item>
                    {state ?
                        <div className='div_piece_state' style={{backgroundColor: getLightcolor(state)}}>{state}</div> :
                        <div></div>}
                </Flex>
                <div className='div_piece_title' onClick={() => this.props.details()}>{title}</div>
                <Flex justify="between">
                    <Item>{type ? `紧急程度：${type}` : ''}</Item>
                    <div>{date ? `规定办结时限：${getDateFormat(date)}` : ''}</div>
                </Flex>
                <BlackSpace size='lg'/>
            </div>
        )
    }
}


