import React, { Component } from 'react'
import { Carousel, NoticeBar } from 'antd-mobile';
import './style.css'
import { GetNoticeTop } from '../../request/request'
import defaultImg from '../../images/1111.jpg'

// async function getData() {
//     let data = await GetNoticeTop()
//     if (data.data && data.data.length > 0) {
//         this.setState({ data: data.data })
//     }
// }

// 首页轮播图
export default class Headcarousel extends Component {
    state = {
        imgHeight: 140,
        carIndex: 1,
        top: 74,
        data: [{ dataFieldMap: { '轮播图片': '' } }]
    }

    componentDidMount() {
        // getData.call(this)
    }

    render() {

        const { tip, noticeTop } = this.props;
        const { carIndex, top } = this.state;
        // let title = tip
        // let size = document.body.clientWidth > 400 ? 17 : 13;
        // if (title && title.length > size) {
        //     title = title.substring(0, size) + '...'
        // }
        let data = noticeTop
        return (
            <div>
                <NoticeBar mode="closable" onClick={() => this.setState({ top: 56 })} icon={null}
                    marqueeProps={{ loop: true, style: { padding: '0 7.5px' } }}>
                    【温馨提示】本系统属非涉密信息系统，严禁存储、处理、传输密码电报及涉密级文件资料等信息内容
                </NoticeBar>
                <div style={{ height: 140 }}>
                    {
                        data && data.length > 0 ?
                            <Carousel
                                dots={false}
                                autoplay={false}
                                infinite
                                beforeChange={(from, to) => this.setState({ carIndex: to + 1 })}
                            >
                                {
                                    data.map((v, i) => {
                                        let imgsrc = v.dataFieldMap['轮播图片']
                                        return <img key={i} alt='' onClick={() => this.getnoticedetails(v.id)}
                                            src={imgsrc && imgsrc.length > 0 ? v.dataFieldMap['轮播图片'][0].fileAddress : 'http://203.195.194.222/itdccs/static/media/1111.3ac167ed.jpg/#/'}
                                            style={{
                                                width: '100%',
                                                height: this.state.imgHeight,
                                                verticalAlign: 'top'
                                            }} />
                                    })
                                }
                            </Carousel> :
                            <img
                                src='http://203.195.194.222/itdccs/static/media/1111.3ac167ed.jpg/#/'
                                style={{
                                    width: '100%',
                                    height: this.state.imgHeight,
                                    verticalAlign: 'top'
                                }} />
                    }

                    <div className='div_carousel_tip' style={{ top: top }}>
                        <span> {data && data.length && data[carIndex - 1].dataFieldMap['标题'] ? carIndex + '.' + this.fontsplt(data[carIndex - 1].dataFieldMap['标题'])
                            : ''}</span>
                        <span>
                            <span>{carIndex}</span>{`/${data.length}`}
                        </span>
                    </div>
                </div>
            </div>
        )
    }

    fontsplt = (title) => {
        if (!title) return ''
        if (title.length > 14) {
            return title.substring(0, 14) + '...'
        } else {
            return title
        }
    }

    getnoticedetails = (res) => {
        this.props.history.push(`/notice/index/${res}`)
    }
}




