import React, { Component } from 'react'
import Handlebench from './Handlebench'
import Dutybench from './Dutybench'
import Leadbench from './Leadbench'
import Nodate from '../../components/Nodate'
import { Userid } from "../../components/Utils";
import PageLoading from '../../components/PageLoading'
import './style.css'
import { getallagentTotal, getprovincapprovalTotal, getspecialreport, getUserInfo, GetNoticeTop } from "../../request/request";

async function getData() {
    let { provincapproval, specialreport, allagentTotal, noticeTop, returnCode, isShowSelect, showname, showid, roleName } = this.state;

    if (Object.keys(allagentTotal).length === 0) {
        let all = await getallagentTotal();
        if (all && all.data) {
            allagentTotal = all.data
        }
        //手动解除引用
        all = null;
    }

    if (Object.keys(noticeTop).length === 0) {
        let notice = await GetNoticeTop()
        if (notice.data && notice.data.length > 0) {
            noticeTop = notice.data
        }
        //手动解除引用
        notice = null;
    }

    let userid = localStorage.getItem('userid')
    //if (!userid) {//第一次
    let userinfo = await getUserInfo();
    localStorage.setItem('userid', userinfo.data.userid)
    isShowSelect = userinfo.data.isShowSelect;
    userid = userinfo.data.userid;
    showid = userinfo.data.userid;
    showname = userinfo.data.name;
    returnCode = userinfo.data.returnCode;
    roleName = userinfo.data.roleName;
    //}
    let params = { 'userid': userid }
    let zb = await getspecialreport(params);
    // let provincapprovalTotal = await getprovincapprovalTotal(params);
    // if (provincapprovalTotal && provincapprovalTotal.data) {
    //     provincapproval = provincapprovalTotal.data
    // }
    if (zb && zb.data) {
        specialreport = zb.data
    }
    //手动解除引用
    zb = null;
    this.setState({ provincapproval, specialreport, allagentTotal, noticeTop, loading: false, userid, roleName, returnCode, isShowSelect, showname, showid })
}

export default class Home extends Component {

    state = {
        specialreport: {},
        provincapproval: {},
        allagentTotal: {},
        isShowSelect: "1",
        userid: "",
        returnCode: "0",
        showid: "",
        showname: "",
        roleName: "",
        noticeTop: [],
        loading: true
    };

    componentDidMount() {
        getData.call(this)
    }

    render() {
        return (<div>
            <PageLoading loading={this.state.loading} />
            {
                this.rest()
            }
        </div>)
    }

    rest = () => {

        const { isShowSelect, returnCode, roleName, loading } = this.state;

        if (returnCode !== "0" && loading === false) {
            return <Nodate msg={'无权限'} />
        }

        switch (roleName) {
            case '省府督查室经办人':
            case '省府办公厅其他业务处室经办人':
                return <Handlebench {...this.state} {...this.props} />;
                break;
            case '省政府领导':
            case '省府办公厅领导':
            case '省府督查室领导':
            case '省府办公厅其他业务处室领导':
                return <Leadbench {...this.state} {...this.props} />;
                break;
            case '责任单位':
                return <Dutybench {...this.state} {...this.props} />;
                break;
            default:
                return <Handlebench {...this.state} {...this.props} />;
                // if (loading) return ''
                //return <Nodate msg={'暂无该角色权限'} />
                break;
        }
    }
}



