import React, {Component} from 'react'
import './style.css'
import {getLightcolor} from '../Utils'

// 督查进度组件
export default class Processdetails extends Component {

    render() {
        const {process} = this.props;
        const Processbox = (restProps) => {
            const {processList, title, ...othercss} = restProps;
            if (processList && processList.length) {
                return processList.map((v, i) =>
                    <div key={i} {...othercss} onClick={() => this.props.restProcess({...v, title})}>
                        <div style={{color: getLightcolor(v.state)}}> {v.num}</div>
                        <div> {v.state}</div>
                    </div>
                )
            }
            return ''
        };

        return (
            <div style={{backgroundColor: 'white'}}>
                {
                    process && process.length ? process.map((p, i) =>
                        <div key={i} className={`div_process div_underline_bottom${i}`}>
                            <p className='div_process_title'>{p.title || '详情'}</p>
                            <div className='flex-container_div' style={{height: i === 0 ? 70 : 78}}>
                                <Processbox processList={p.processList} className='flex_processs' title={p.title}/>
                            </div>
                        </div>
                    ) : ''}
            </div>
        )
    }
}


