import React from 'react';
import './style.css'
import {ActivityIndicator} from 'antd-mobile';

export default (restprops) =>
    <div className={'div_pageloading warp'} style={{display: restprops.loading ? 'hidden' : 'none'}}>
        <div className={'bg_loading'}></div>
        <ActivityIndicator animating={restprops.loading} toast text="加载中" size={'large'}/>
    </div>;


