import React, {Component} from 'react'
import {Tabs} from 'antd-mobile';
import TaskList from './taskdetails'
import icon_care from '../../images/icon_care@2x.png'
import icon_cared from '../../images/icon_cared@2x.png'
import icon_praise from '../../images/icon_praise@2x.png'
import icon_praised from '../../images/icon_praised@2x.png'

export default class Taskbox extends Component {

    state = {
        loading: true
    };

    componentWillReceiveProps(nextProps) {
        setTimeout(() => {
            if (this.state.loading) {
                this.setState({loading: false})
            }
        }, 300)
    }

    render() {
        const {tabs, state, tab, defaultActiveKey} = this.props;
        const Tbox = (restProps) => <TaskList {...restProps} {...this.state} {...this.props}>
            {(v, i) => this.setAttrs(v)}
        </TaskList>;
        return (
            <div>
                {
                    state && state.length > 0 ?
                        <Tbox {...tab.filter(v => v.t === state)[0]} taskinfo={() => this.props.taskinfo()}/> :
                        <Tabs tabs={tabs} swipeable={true}
                              onChange={(tab) => this.handleTabclick(tab, defaultActiveKey, this.props.tab)}>
                            {
                                tabs.map((v, i) =>
                                    <Tbox key={i} {...tab.filter(t => t.t === v.title.props.children)[0]}
                                          taskinfo={(id) => this.props.taskinfo(id)}/>
                                )
                            }
                        </Tabs>
                }
            </div>
        )
    }

    handleTabclick = (tab, defaultActiveKey, propsTab) => {
        // console.log(propsTab)
        // if (propsTab.findIndex(v => v.t === tab.title.props.children) === -1) {
        //     this.setState({loading: true})
        // }
        this.props.setStatetype(tab)
    };

    setAttrs = (v, t = false) => {
        let icon = '';
        if (v !== '批示') {
            if (v === '关注') {
                icon = <img alt={''} src={t ? icon_cared : icon_care}/>
            } else {
                icon = <img alt={''} src={t ? icon_praised : icon_praise}/>
            }
        }
        return <div key={v}>{icon}{v}</div>
    }
}


