import React, { Component } from 'react'
import PageLoading from '../../components/PageLoading'
import { GetNoticeInfo } from '../../request/request'
import { getFileicon} from '../../components/Utils'
import './style.css'

async function getdata() {
    console.log(this.props)
    let { id } = this.props.match.params
    console.log(id)
    if (id) {
        let d = GetNoticeInfo({ 'id': id })
        d.then(v => {
            this.setState({ data: v.data })
            let text = this.state.data['通告内容']
            this.refs.noticeHTML.innerHTML = text ? text.replace(new RegExp('cms/v1', 'g'), '/cms/v1') : ""
        })
    }
}
export default class NoticeInfo extends Component {

    state = {
        data: {}
    }
    componentDidMount() {
        getdata.call(this)
    }
    render() {
        const { data } = this.state
        let reg =new RegExp('/','g')
        console.log(data)
        return (<div>
            <div className="div_notice_title">
                <h2>{data['标题']}</h2>
                <div><b>{data['操作时间']?data['操作时间'].replace(reg,'-'):''}</b></div>
                <b>{data['来源部门'] ? '来源：' + data['来源部门'] : ''}</b>
            </div>
            <div ref='noticeHTML' className='div_notice_html'>
                <PageLoading loading={true} />
            </div>
            <br></br>
            <div className="div_notice_file">
                <b>附件</b>
                <hr></hr>
                {
                    this.getfileMap(data['通告附件'])
                }
            </div>
        </div>)
    }
    getfileMap = (f) => {
        console.log(f)
        if (f && f.length > 0) { 
          return  f.map(v =>
              <div className="div_notice_filebox" onClick={()=>window.open(v.fileAddress)}>
                  <div>
                      <img src={getFileicon(v.type)}></img>
                  </div>
                  <div>{v.fileName.substring(0,v.fileName.indexOf('.'))}</div>
                </div>)
        }
        return <p>暂无附件</p>
    }
}




