import React from 'react'
import dw from '../../images/icon_newlybuilt copy 2@2x.png'
import rw from '../../images/icon_newlybuilt copy 3@2x.png'
import gd from '../../images/icon_newlybuilt copy 4@2x.png'
import ld from '../../images/icon_newlybuilt copy 5@2x.png'
import lx from '../../images/icon_newlybuilt copy@2x.png'
import word from '../../images/word.png'
import excel from '../../images/excel.png'
import txt from '../../images/txt.png'
import pdf from '../../images/pdf.png'
import weizhi from '../../images/weizhi.png'
import image from '../../images/image.png'

export const Userid = () => {
    //return { 'userid': '074nycwsg7n0ce0ohgvo8w' }
    let userid =localStorage.getItem('userid')
    return {'userid': userid}
};

export const getFileicon = (type) => {
    switch (type) {
        case 'txt':
            return txt;
            break;
        case 'excel':
            return excel;
            break;
        case 'word':
            return word;
            break;
        case 'image':
            return image;
            break;
        case 'pdf':
            return pdf;
            break;
        default:
            return weizhi;
            break;
    }
}

export const getLightcolor = (state) => {
    let color = 'block';
    switch (state) {
        case '逾期未结':
            color = '#E64919';
            break;
        case '正常在办':
            color = '#0270E0';
            break;
        case '按时在办':
            color = '#00CBBC';
            break;
        case '按时办结':
        case '按时报送':
            color = '#00C952';
            break;
        case '即将到期':
            color = '#F9A11B';
            break;
    }
    return color
};

export const restdept = (d, name) => {
    if (d) {
        if (d[name] instanceof Array) {
            let dept = ''
            d[name].map(v => dept += v + ',');
            return dept.substring(0, dept.length - 1)
        } else {
            if (!d[name]) return '';
            return d[name].replace('[', '').replace(']', '');
        }
    }
    return ''
};

//菜单Icon
export const getTaskicon = (task) => {
    let icon = '';
    switch (task) {
        case '立项交办':
            icon = lx;
            break;
        case '领导视图':
            icon = ld;
            break;
        case '任务视图':
            icon = rw;
            break;
        case '单位视图':
            icon = dw;
            break;
        case '归档查询':
            icon = gd;
            break;
        default:
            break;
    }
    return icon
};

export const getRolecomponent = (t, r) => {
    switch (r) {
        case '领导':
            t.props.history.push({ pathname: '/home/Leadbench' });
            break;
        case '经办人':
            t.props.history.push({ pathname: '/home/Handlebench' });
            break;
        case '责任单位':
            t.props.history.push({ pathname: '/home/Dutybench' });
            break;
        default:
            break;
    }
};

export const getFilterdata = (source, fun) => {
    return source.filter(v => fun)
};

export const LeadtaskList = ['立项交办', '单位视图', '任务视图', '归档查询'];
export const DutytaskList = ['任务视图', '归档查询', '', ''];
export const HandletaskList = ['立项交办', '领导视图', '任务视图', '单位视图', '归档查询', "", "", ""];

//时间格式转换（时间截——>标准时间格式）
export const getDateFormat = (date, longtime = false) => {
    if (String(date).indexOf('-') > 0) return date.substring(0, 10);
    date = parseInt(date);
    if (date > 0) {
        let t = new Date(date);
        if (t === 'Invalid Date') {
            t = new Date();
        }

        let y = t.getFullYear();
        let m = t.getMonth() + 1;
        let d = t.getDate();
        let h = t.getHours();
        let mm = t.getMinutes();
        let s = t.getSeconds();
        const add = (md) => md < 10 ? `0${md}` : md;
        if (longtime) {
            return y + '-' + add(m) + '-' + add(d) + ' ' + add(h) + ':' + add(mm) + ':' + add(s);
        }
        return y + '-' + add(m) + '-' + add(d);
    }
    return ''
};

export const EnumTaskstate = (state) => {
    switch (state) {
        case '逾期未结':
            return 1;
            break;
        case '按时在办':
            return 2;
            break;
        case '即将到期':
            return 3;
            break;
        case '已办结':
            return 4;
            break;
        default:
            return 0;
            break
    }
};


