import React, {
    Component
} from 'react'
import Headcarousel from '../../../components/Headcarousel'
import Taskbox from '../../../components/Taskbox'
import BlackSpace from '../../../components/BlackSpace'
import Processdetails from '../../../components/Processdetails'
import Listbox from '../../../components/List'
import {
    HandletaskList,
    Userid
} from '../../../components/Utils'
import {
    getspecialreport,
    getprovincapprovalTotal
} from "../../../request/request";

let process = [{
    title: '经办',
    processList: [{
        state: '逾期未结',
        num: 0
    }, {
        state: '正常在办',
        num: 0
    }]
},
{
    title: '全部',
    processList: [{
        state: '逾期未结',
        num: 0
    }, {
        state: '正常在办',
        num: 0
    }]
}
]

async function getdata() {
    let provincapproval = {}
    let provincapprovalTotal = await getprovincapprovalTotal(Userid());
    if (provincapprovalTotal && provincapprovalTotal.data) {
        provincapproval = provincapprovalTotal.data
    }
    this.setState({
        provincapproval
    })
}
export default class Handlebench extends Component {

    state = { provincapproval: {} };

    componentDidMount() {
        document.title = '电子督查系统';
        getdata.call(this)
    }

    render() {
        const { allagentTotal, specialreport } = this.props;
        const { provincapproval } = this.state
        process[0].processList[0].num = provincapproval.yqwj || 0;
        process[0].processList[1].num = provincapproval.aszb || 0;
        process[1].processList[0].num = allagentTotal.yqwj || 0;
        process[1].processList[1].num = allagentTotal.aszb || 0;
        return (
            <div>
                <Headcarousel tip='广州、湛江市提请省支持事项进展情况持事项进展情况持事项进展情况' {
                    ...this.props
                }
                />
                <Taskbox taskList={
                    HandletaskList
                }
                />
                <BlackSpace size='lg' />
                <Processdetails title='省长批示件'
                    process={
                        process
                    }
                    restProcess={
                        (e) => this.props.history.push(`/view/dcpiece/true/false/${JSON.stringify(e)}/领导视图`)
                    }
                />
                <BlackSpace size='lg' />
                <Listbox title='督查专报' {
                    ...specialreport
                }
                />
                <Listbox title='督查通报'
                    msg='广州、湛江市提请省支持事项进展情况广州、湛江市提请省支持事项进展情况' />
                <BlackSpace size='lg' />
            </div>
        )
    }
}


