import React, {Component} from 'react'
import {Flex, Tabs} from 'antd-mobile';
import Tasksort from '../../../components/Utils/tasksort'
import PageLoading from '../../../components/PageLoading'
import {getTaskTotal} from '../../../request/request'
import {Userid} from "../../../components/Utils";
import './style.css'

async function getData() {
    let params = {...Userid(), type: 1};
    let d1 = await getTaskTotal(params);
    params.type = 2
    let d2 = await getTaskTotal(params);
    if (d1 && d1.data) {
        this.setState({tab1: d1.data,loading:false})
    }
    if (d2 && d2.data) {
        this.setState({tab2: d2.data,loading:false})
    }

}

const tabs = [
    {title: '省直部门', sub: 1},
    {title: '地市政府', sub: 2}
]
const flexstyle = {justify: 'between', className: 'div_unit_flex', backgroundColor: '#F4F4F4'};
export default class Unitview extends Component {

    state = {
        scort: 'down',
        tab1: [],
        tab2: [],
        loading: true
    };

    componentDidMount() {
        document.title = '单位视图';
        getData.call(this)
    }

    render() {
        const {scort, tab1, tab2,loading} = this.state;
        tab1.sort((a, b) => scort === 'top' ? a.totalnum - b.totalnum : b.totalnum - a.totalnum);
        tab2.sort((a, b) => scort === 'top' ? a.totalnum - b.totalnum : b.totalnum - a.totalnum);
        const flexs = (restProps,tabs) =>
            <Flex {...flexstyle}>
                <Flex.Item>{restProps.deptname}</Flex.Item>
                <div
                    onClick={() => this.props.history.push('/view/dcpiece/false/false/' + JSON.stringify({dept: restProps.deptname,depttype:tabs.title})+'/单位视图')}>{restProps.totalnum}</div>
            </Flex>;

        return (
            <div>
                <PageLoading loading={loading}/>
                <Tabs tabs={tabs}>
                    {
                        tabs.map(v => <div className='div_unitview'>
                            <Tasksort scort={scort} tip={{tip1: '部门名称', tip2: '督查任务数'}}
                                      updateScore={(scort) => this.setState({scort})}/>
                            {v.sub === 1 ? tab1.map(t => flexs(t,v)) : tab2.map(t => flexs(t,v))}
                        </div>)
                    }
                </Tabs>
            </div>
        )
    }
}



