import React, {Component} from 'react'
import {getLightcolor, Userid} from "../../../components/Utils";
import BlackSpace from '../../../components/BlackSpace'
import PageLoading from '../../../components/PageLoading'
import Nodate from '../../../components/Nodate'
import {Flex, List} from 'antd-mobile';
import {getTaskDeptFKInfo} from "../../../request/request";

const {Item} = List
const {Brief} = Item

async function getData() {
    let attrs = JSON.parse(this.props.match.params.id);
    let params = {...Userid(), 'id': attrs.id};
    let fkdata = await getTaskDeptFKInfo(params)
    this.setState({loading: false, fkdata: fkdata.data})

}

export default class Taskfeedback extends Component {

    state = {
        fkdata: [],
        loading: true
    }

    componentDidMount() {
        getData.call(this)
        document.title = '任务进度反馈'
    }

    render() {
        const bgcolor = (s) => {
            return {backgroundColor: getLightcolor(s)}
        }
        const setItemstyle = () => {
            return {multipleLine: true, align: 'top'}
        }
        const setflexstyle = () => {
            return {justify: 'between'}
        }
        const {fkdata, loading} = this.state
        return (
            <div>
                <PageLoading loading={loading}/>
                {
                    fkdata && fkdata.data ?
                        <List>
                            <Item {...setItemstyle}>
                                甲单位
                                <Brief>
                                    <Flex {...setflexstyle}>
                                        <Flex.Item>反馈时限：2018/11/22 11:39:09</Flex.Item>
                                        <div className='div_piece_state' style={bgcolor('逾期未结')}>{'逾期未结'}</div>
                                    </Flex>
                                </Brief>
                            </Item>
                            <BlackSpace size='lg'/>
                            <Item {...setItemstyle}>
                                乙单位
                                <Brief>
                                    <Flex {...setflexstyle}>
                                        <Flex.Item>反馈时限：2018/11/22 11:39:09</Flex.Item>
                                        <div className='div_piece_state' style={bgcolor('按时报送')}>{'按时报送'}</div>
                                    </Flex>
                                    <div className={'div_feedback_msg'}>
                                        <div>1.存在困难及原因分析：审核流程差异大，统一规范过程中遇到阻力。</div>
                                        <div>2.存在困难及原因分析：审核流程差异大，统一规范过程中遇到阻力。</div>
                                    </div>
                                </Brief>
                            </Item>
                        </List> : !loading?<Nodate msg={'无数据'}/>:''
                }
            </div>
        )
    }
}


