import React, {Component} from 'react'
import {Flex} from 'antd-mobile';
import sjt from '../../images/shangjiantou.svg'
import xjt from '../../images/xiajiantou.svg'
import sjtf from '../../images/shangjiantouF.svg'
import xjtf from '../../images/xiajiantouF.svg'

export default class Tasksort extends Component {


    render() {
        let {scort,tip} = this.props
        let rest = scort === 'down' ? 'top' : 'down'
        return (
            <Flex className='div_unit_flex div_unit_tip' justify='around'>
                <div style={{marginLeft:10}}>{tip.tip1}</div>
                <div onClick={() => this.props.updateScore(rest)}>{tip.tip2}
                    <span className='span_tools'>
                        <img src={scort === 'down' ? sjtf : sjt} alt=''/>
                        <img src={scort === 'top' ? xjtf : xjt} alt=''/>
                    </span>
                </div>
            </Flex>)
    }
}


