import React, {Component} from 'react'
import BlackSpace from '../../components/BlackSpace'
import PageLoading from '../../components/PageLoading'
import {getLightcolor} from "../Utils";
import Nodate from '../../components/Nodate'
import {Flex} from 'antd-mobile';
import './style.css'


export default class TaskList extends Component {

    render() {
        const {data, attrs, depttype, defaultActiveKey, loading} = this.props
        return (
            <div>
                <BlackSpace size='lg'/>
                <PageLoading loading={loading}/>
                {
                    data && data.length > 0 ? data.map((v, index) => <div key={index}>
                        <div className='div_border_bottom div_piece_code'>
                            <Flex justify="between" align='start' style={{paddingLeft: 0, paddingBottom: 0}}>
                                <div className='div_piece_title'
                                     onClick={() => this.props.taskinfo(v['id'])}>{v['督查件标题']}</div>
                                <div className='div_piece_state'
                                     style={{backgroundColor: getLightcolor(defaultActiveKey)}}>{defaultActiveKey}</div>
                            </Flex>
                            <Flex align={'start'} style={{fontSize: 14, color: '#000000'}}>
                                <div style={{display: 'Flex', justifyContent: 'start'}}>
                                    <div className='div_circle'>
                                        <div></div>
                                    </div>
                                    {depttype}：
                                </div>
                                <Flex.Item>
                                    {
                                        Array.isArray(v['牵头单位名称']) ? v['牵头单位名称'].map((d, i) => <div key={i}>{d}</div>) :
                                            <div>{v['牵头单位名称']}</div>
                                    }
                                </Flex.Item>
                            </Flex>
                        </div>
                        <Flex justify="end" className='div_piece_code div_tasktools'>
                            {
                                Array.isArray(attrs) && attrs.length > 0 ? attrs.map((v, i) => this.props.children(v, i)) : ''
                            }
                        </Flex>
                        <BlackSpace size='lg'/>
                    </div>) : <Nodate msg={'无数据'}/>
                }
            </div>
        )
    }
}


