import React, {Component} from 'react'
import {Flex} from 'antd-mobile';
import {getYearTotal} from '../../../request/request'
import Tasksort from '../../../components/Utils/tasksort'

const flexstyle = {justify: 'around', className: 'div_unit_flex', backgroundColor: '#F4F4F4',align:'center'};

async function getData() {
   let d =await getYearTotal();
}
export default class Finishview extends Component {
    state = {
        scort: 'down'
    };

    render() {
        const {scort} = this.state;
        let tabs = "按时办结,超期办结"
        const tab1 = [{dept: '2018年', num: 0}, {dept: '2017年', num: 0}];
        const flexs = (restProps, i) =>
            <Flex key={i} {...flexstyle} >
                <div>{restProps.dept}</div>
                <div onClick={() => this.props.history.push(`/view/dcpiece/false/${tabs}/false/归档查询`)}>{restProps.num}</div>
            </Flex>

        return (
            <div className='div_unitview'>
                <Tasksort scort={scort} tip={{tip1: '年份', tip2: '督查件数'}}
                          updateScore={(scort) => this.setState({scort})}/>
                {
                    tab1.map((v, i) => flexs(v, i))
                }
            </div>
        )
    }
}


