import React, { Component } from 'react'
import { List } from 'antd-mobile';
import './style.css'
import { getspecialreport } from "../../request/request";
import { Userid } from "../Utils";

const Item = List.Item;
const Brief = Item.Brief;

async function getData() {
    let zb = await getspecialreport(Userid());
    if (zb && zb.data) {
        this.setState({ specialreport: zb.data })
    }
}

// 自定义列表，后续可扩展更多功能
export default class Listbox extends Component {

    componentDidMount() {
        getData.call(this)
    }

    state = {
        specialreport: {}
    };

    render() {
        const { specialreport } = this.state;
        const { title, url } = this.props;
        console.log(this.props)
        return (
            <div>
                <List className="my-list">
                {/* <Item extra="更多" multipleLine arrow="horizontal" onClick={() => { */}
                    <Item  multipleLine onClick={() => {
                    }}>
                        <div className="span_listbox_title">{title}</div>
                    </Item>
                    <Item multipleLine>
                        <Brief>
                            <div onClick={()=>this.opendoc(url)} className='div_list_brief'>{this.props['报告标题'] ? this.props['报告标题'] || '' : ''}</div>
                        </Brief>
                    </Item>
                </List>
            </div>
        )
    }
    opendoc = (url) => {
        console.log(1)
        return window.open(url)
    }
}


