import React, {Component} from 'react'
import Headcarousel from '../../../components/Headcarousel'
import Taskbox from '../../../components/Taskbox'
import BlackSpace from '../../../components/BlackSpace'
import Processdetails from '../../../components/Processdetails'
import Listbox from '../../../components/List'
import {DutytaskList, Userid} from '../../../components/Utils'
import {getJoinDeptCount, getLeadDeptCount} from "../../../request/request";

let process = [
    {title: '承办牵头督查任务', processList: [{state: '逾期未结', num: 0}, {state: '正常在办', num: 0}]},
    {title: '承办参加督查任务', processList: [{state: '逾期未结', num: 0}, {state: '正常在办', num: 0}]}
]

async function getData() {
    let d = await  getJoinDeptCount(Userid());
    let e = await  getLeadDeptCount(Userid());
    process[0].processList[0].num = d.data['逾期未结'];
    process[0].processList[1].num = d.data['正常在办'];
    process[1].processList[0].num = e.data['逾期未结'];
    process[1].processList[1].num = e.data['正常在办']
}

export default class Dutybench extends Component {

    componentDidMount() {
        document.title = '电子督查系统';
    }

    render() {
        return (
            <div style={{backgroundColor: 'white'}}>
                <Headcarousel tip='广州、湛江市提请省支持事项进展情况持事项进展情况持事项进展情况' {...this.props}/>
                <Taskbox taskList={DutytaskList}/>
                <BlackSpace size='lg'/>
                <Processdetails title='省长批示件' process={process}
                                restProcess={(e) => this.props.history.push(`/view/dcpiece/true/false/${JSON.stringify(e)}/领导视图`)}/>
                <BlackSpace size='lg'/>
                <Listbox title='督查通报' msg='广州、湛江市提请省支持事项进展情况广州、湛江市提请省支持事项进展情况'/>
                <BlackSpace size='lg'/>
            </div>
        )
    }
}



