import React, {Component} from 'react'
import Dcpiecebox from '../../../components/Dcpiece'
import PageLoading from '../../../components/PageLoading'
import {SearchBar, Tabs} from 'antd-mobile';
import {GetDCJInfo, getDeptdcjInfo, getProvLeadInfo, GetRespLeadInfo,getpeopletask} from '../../../request/request'
import {Userid} from "../../../components/Utils";
import Nodate from '../../../components/Nodate'
import './style.css'

async function getData() {
    let d = [];
    let params = {}
    let v = this.props.match.params.view
    let type = JSON.parse(this.props.match.params.type)
    console.log(type)
    if (v === '任务视图') {
        params = {...Userid()}
        d = await GetDCJInfo(params)
    } else if (v === '单位视图') {
        params = {deptname: type.dept};
        d = await getDeptdcjInfo(params)
    } else {
        params = {'type': type.state === '正常在办' ? 2 : 1, ...Userid()};
        // if (type.dept) {
        //     params = {deptname: type.dept};
        //     d = await getDeptdcjInfo(params)
        // } else {
        if (type.title === '省长批示件') {
            d = await getProvLeadInfo(params)
        } else {
            params.peotype ='1'
            d = await getpeopletask(params)
        }
        // }
        //if (d && Object.keys(d.data).length !== 0) {
    }

    this.setState({data: d.data, loading: false})
    //}

}

export default class Dcpiece extends Component {
    state = {
        searchText: '',
        title: '',
        data: [],
        loading: true
    };

    componentDidMount() {
        document.title = '督查件';
        getData.call(this)
    }

    render() {
        const {searchText, data, loading} = this.state;
        let {tabs, attrs} = this.props.match.params;
        let tabarray = [];
        if (tabs && tabs !== "false") {
            tabs.split(',').map((v, i) => tabarray.push({title: v, sub: String(i)}))
        }

        const Rest = (restProps) => {
            if (tabarray && tabarray.length > 0) {
                return this.tabcomb(tabarray, attrs)
            } else {
                let d = data;
                if (searchText && searchText.length > 0) {
                    d = data.filter((r, i) => String(r['督查件标题']).indexOf(searchText) !== -1);
                }
                if (!loading && (!d || Object.keys(d).length==0)) {
                    return <Nodate msg={'无数据'}/>
                }
                return d.map((v, i) =>
                    <Dcpiecebox key={i} state={v['督查件办理状态'] || v.state} code={v['编号']} title={v['督查件标题']}
                                type={v['紧急程度']}
                                date={v['限时接收']} details={() => this.goto(v, attrs)}/>)
            }
        };

        return (
            <div>
                <SearchBar placeholder='搜索' onChange={searchText => this.setState({searchText})}/>
                <PageLoading loading={loading}/>
                <Rest/>
            </div>
        )
    }


    goto = (v, attrs, state = '') => {
        let type = JSON.parse(this.props.match.params.type);
        let params = {id: v['id'], type: attrs, deptname: type.dept,depttype:type.depttype, 'view': this.props.match.params.view};
        this.props.history.push({
            pathname: `/view/Task/${JSON.stringify(params)}/` + state,
        })
    };

    tabcomb = (tabarray, attrs) => {
        let {searchText, data} = this.state;
        return <Tabs tabs={tabarray}>
            {
                tabarray.map(v => <div>
                    {
                        data.filter((r, i) => r['state'] === v.title && String(r['title']).indexOf(searchText) !== -1).map((v, i) =>
                            <Dcpiecebox key={i} {...v} details={() => this.goto(v, attrs, v.state)}/>)
                    }
                </div>)
            }
        </Tabs>
    }
}


